'use client';

import React, { useState } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { useAppContext } from '../context/AppContext';
import { signIn } from 'next-auth/react';
import '../App.css';

interface AuthFormProps {
  onClose?: () => void;
}

const AuthForm: React.FC<AuthFormProps> = ({ onClose = () => {} }) => {
  const { error, setError, loading, login, register } = useAppContext();
  const router = useRouter();
  const searchParams = useSearchParams();
  const redirectParam = searchParams?.get('redirect') || '/';

  const [isLogin, setIsLogin] = useState(true);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    confirmPassword: ''
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
    // Clear error when user starts typing
    if (error?.auth) setError('auth', '');
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    try {
      if (isLogin) {
        // Login validation
        if (!formData.email || !formData.password) {
          throw new Error('Please fill in all fields');
        }

        const result = await login({
          email: formData.email,
          password: formData.password
        });

        if (result) {
          // Close modal and navigate to redirect URL or home page after login
          const targetPath = redirectParam && redirectParam !== '/' ? decodeURIComponent(redirectParam) : '/';
          if (targetPath === '/') {
            // If redirecting to home, just close the modal
            onClose();
          } else {
            // Otherwise navigate to the redirect page
            router.push(targetPath);
          }
        }
      } else {
        // Registration validation
        if (!formData.name || !formData.email || !formData.password) {
          throw new Error('Please fill in all fields');
        }

        if (formData.password !== formData.confirmPassword) {
          throw new Error('Passwords do not match');
        }

        // Password strength validation
        if (formData.password.length < 6) {
          throw new Error('Password must be at least 6 characters long');
        }

        if (!/(?=.*[a-z])(?=.*[A-Z])(?=.*\d)/.test(formData.password)) {
          throw new Error('Password must contain at least one lowercase letter, one uppercase letter, and one digit');
        }

        const result = await register({
          name: formData.name,
          email: formData.email,
          password: formData.password
        });

        if (result) {
          // Close modal and navigate to redirect URL or home page after registration
          const targetPath = redirectParam && redirectParam !== '/' ? decodeURIComponent(redirectParam) : '/';
          if (targetPath === '/') {
            // If redirecting to home, just close the modal
            onClose();
          } else {
            // Otherwise navigate to the redirect page
            router.push(targetPath);
          }
        }
      }
    } catch (err: any) {
      console.error('Auth error:', err);
      setError('auth', err.message || 'An error occurred. Please try again.');
    }
  };

  // Google Sign In handler
  const handleGoogleSignIn = async () => {
    try {
      // Use NextAuth to sign in with Google - don't redirect immediately
      // Instead, use callback to handle the response
      const result = await signIn('google', {
        redirect: false, // Don't redirect, use callback to handle response
      });

      if (result?.error) {
        console.error('Google sign-in error:', result.error);
        setError('auth', result.error || 'Google authentication failed. Please try again.');
      } else if (result?.ok) {
        // Google sign-in was successful, close modal
        // The session will be handled by NextAuth and sync to AppContext
        onClose();
      }
    } catch (error: any) {
      console.error('Google login error:', error);
      setError('auth', 'Google authentication failed. Please try again.');
    }
  };

  return (
    <div className="auth-page">
      <div className="auth-container-elegant">
        <button 
          onClick={onClose} 
          className="close-btn absolute top-6 right-6 w-10 h-10 rounded-full bg-gray-100 hover:bg-gray-200 flex items-center justify-center z-10"
        >
          <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-gray-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
        
        <div className="auth-header-elegant text-center mb-8">
          <div className="auth-title">
            <h2 className="auth-main-title text-3xl font-bold text-gray-900 mb-3">
              {isLogin ? 'Welcome Back' : 'Create Account'}
            </h2>
            <p className="auth-subtitle text-gray-600">
              {isLogin ? 'Sign in to continue your journey' : 'Join our community today'}
            </p>
          </div>
        </div>

        {error?.auth && (
          <div className="mb-6 p-4 bg-red-50 border border-red-200 text-red-700 rounded-xl">
            <div className="flex items-center">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7 4a1 1 0 11-2 0 1 1 0 012 0zm-1-9a1 1 0 00-1 1v4a1 1 0 102 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
              </svg>
              <span>{error.auth}</span>
            </div>
          </div>
        )}

        <form onSubmit={handleSubmit} className="auth-form-elegant">
          {!isLogin && (
            <div className="form-group-elegant mb-5">
              <label htmlFor="name" className="form-label block text-sm font-medium text-gray-700 mb-2">Full Name</label>
              <input
                type="text"
                id="name"
                name="name"
                placeholder="Enter your full name"
                value={formData.name}
                onChange={handleChange}
                required={!isLogin}
                className="w-full px-5 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-amber-500 focus:border-amber-500 transition-all duration-300 shadow-sm font-medium"
              />
            </div>
          )}

          <div className="form-group-elegant mb-5">
            <label htmlFor="email" className="form-label block text-sm font-medium text-gray-700 mb-2">Email Address</label>
            <input
              type="email"
              id="email"
              name="email"
              placeholder="Enter your email"
              value={formData.email}
              onChange={handleChange}
              required
              className="w-full px-5 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-amber-500 focus:border-amber-500 transition-all duration-300 shadow-sm font-medium"
            />
          </div>

          <div className="form-group-elegant mb-5">
            <label htmlFor="password" className="form-label block text-sm font-medium text-gray-700 mb-2">Password</label>
            <input
              type="password"
              id="password"
              name="password"
              placeholder="Enter your password"
              value={formData.password}
              onChange={handleChange}
              required
              className="w-full px-5 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-amber-500 focus:border-amber-500 transition-all duration-300 shadow-sm font-medium"
            />
            {!isLogin && (
              <div className="mt-3 text-xs text-gray-600">
                <p className="font-medium mb-1">Password must contain:</p>
                <ul className="pl-5 space-y-1">
                  <li className={`flex items-center ${formData.password.length >= 6 ? 'text-green-600' : 'text-gray-500'}`}>
                    <span className="mr-2">{formData.password.length >= 6 ? 'âœ“' : 'â€¢'}</span>
                    At least 6 characters
                  </li>
                  <li className={`flex items-center ${/(?=.*[a-z])/.test(formData.password) ? 'text-green-600' : 'text-gray-500'}`}>
                    <span className="mr-2">{/(?=.*[a-z])/.test(formData.password) ? 'âœ“' : 'â€¢'}</span>
                    One lowercase letter
                  </li>
                  <li className={`flex items-center ${/(?=.*[A-Z])/.test(formData.password) ? 'text-green-600' : 'text-gray-500'}`}>
                    <span className="mr-2">{/(?=.*[A-Z])/.test(formData.password) ? 'âœ“' : 'â€¢'}</span>
                    One uppercase letter
                  </li>
                  <li className={`flex items-center ${/(?=.*\d)/.test(formData.password) ? 'text-green-600' : 'text-gray-500'}`}>
                    <span className="mr-2">{/(?=.*\d)/.test(formData.password) ? 'âœ“' : 'â€¢'}</span>
                    One number
                  </li>
                </ul>
              </div>
            )}
          </div>

          {!isLogin && (
            <div className="form-group-elegant mb-6">
              <label htmlFor="confirmPassword" className="form-label block text-sm font-medium text-gray-700 mb-2">
                Confirm Password
              </label>
              <input
                type="password"
                id="confirmPassword"
                name="confirmPassword"
                placeholder="Confirm your password"
                value={formData.confirmPassword}
                onChange={handleChange}
                required={!isLogin}
                className="w-full px-5 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-amber-500 focus:border-amber-500 transition-all duration-300 shadow-sm font-medium"
              />
            </div>
          )}

          <button
            type="submit"
            className={`w-full px-6 py-4 rounded-xl text-white font-semibold shadow-lg transition-all duration-300 ${
              loading?.auth 
                ? 'bg-gray-400 cursor-not-allowed' 
                : isLogin 
                  ? 'bg-gradient-to-r from-amber-500 to-amber-600 hover:from-amber-600 hover:to-amber-700' 
                  : 'bg-gradient-to-r from-amber-500 to-amber-600 hover:from-amber-600 hover:to-amber-700'
            }`}
            disabled={loading?.auth}
          >
            {loading?.auth ? (
              <span className="flex items-center justify-center">
                <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
                Processing...
              </span>
            ) : (
              <span>{isLogin ? 'Sign In' : 'Create Account'}</span>
            )}
          </button>
        </form>

        <div className="my-6 flex items-center">
          <div className="flex-grow border-t border-gray-200"></div>
          <span className="mx-4 text-gray-500 text-sm">or</span>
          <div className="flex-grow border-t border-gray-200"></div>
        </div>

        {/* Google Sign In Button */}
        <div className="google-auth-section-elegant mb-6">
          <button
            onClick={handleGoogleSignIn}
            className="w-full flex items-center justify-center px-6 py-4 border-2 border-gray-300 rounded-xl hover:border-amber-400 transition-colors duration-300 font-medium text-gray-700 bg-white shadow-sm"
          >
            <svg width="20" height="20" viewBox="0 0 24 24" className="mr-3">
              <path
                d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
                fill="#4285F4"
              />
              <path
                d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
                fill="#34A853"
              />
              <path
                d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
                fill="#FBBC05"
              />
              <path
                d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
                fill="#EA4335"
              />
            </svg>
            Continue with Google
          </button>
        </div>

        <div className="auth-toggle-elegant text-center">
          <p className="text-gray-600">
            {isLogin ? "Don't have an account? " : "Already have an account? "}
            <button
              className="text-amber-600 hover:text-amber-800 font-semibold ml-1"
              onClick={() => {
                setIsLogin(!isLogin);
                setError('auth', '');
              }}
            >
              {isLogin ? 'Sign Up' : 'Sign In'}
            </button>
          </p>
        </div>
      </div>
    </div>
  );
};

export default AuthForm;